import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Executar_manutencao } from '../../../modelo/Executar_manutencao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'executar_manutencao-dados-details',   templateUrl: 'executar_manutencao_dadosDetails.component.html' }) export class Executar_manutencaoDadosDetailsComponent implements OnInit {    executar_manutencao: Executar_manutencao;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.executar_manutencao.Executar_manutencao";                 let id = +params['id'];         this.executar_manutencao = new Executar_manutencao();         this.executar_manutencao.cd_executar_manutencao = id;         o.objetoJson = this.executar_manutencao;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a executar_manutencao. '; }      this.executar_manutencao = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


